* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI",  "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: #e2e1e0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  /* text-align: center; */
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

button {
  margin: 0;
  padding: 0 16px;
  border: none;
  font-size: 16px;
  height: 36px;
  line-height: 36px;
  cursor: pointer;
  text-decoration: none;
  color: #fff;
  background-color: #50b6ab;
  text-align: center;
  letter-spacing: .5px;
  border-radius: 2px;
}

button:hover {
  background-color: #7dc3bc;
}

.main-container {
  margin-left: 55px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.main-container.change {
  margin-left: 220px;
}

@media only screen and (max-width: 600px) {
  .main-container.change {
    margin-left: 55px;
  }
}

.side-menu {
  height: 100%;
  width: 55px;
  padding: 10px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  /* transition: 0.5s; */
}

.side-menu a {
  padding: 5px 5px 5px 0px;
  text-decoration: none;
  font-size: 25px;
  display: block;
  color: #818181;
  /* min-width: 200px; */
}

.side-menu__item {
  color: #818181;
  cursor: pointer;
  overflow: hidden;
  max-height: 48px;
}

.side-menu__item:hover,
.side-menu a:hover {
  color: #f1f1f1;
  background-color: #363940;
}

.side-menu.opened{
  width: 220px;
}

.side-menu__icon {
  width: 35px;
  font-size: 35px;
  text-align: center;
  margin-right: 10px;
}

.side-menu__time-tracker::before {

}

.side-menu__pomodoro::before {

}

.side-menu__pomodoro::before {

}

.hamburger {
  display: inline-block;
  cursor: pointer;
  /* transition: 0.5s; */
}

.hamburger__bar1,
.hamburger__bar2,
.hamburger__bar3 {
  width: 35px;
  height: 5px;
  background-color: #fff;
  margin: 6px 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.hamburger.change {
  margin-left: 170px;
}

.change .hamburger__bar1 {
  -webkit-transform: rotate(-45deg) translate(-8px, 7px);
  transform: rotate(-45deg) translate(-8px, 7px);
}

.change .hamburger__bar2 {opacity: 0;}

.change .hamburger__bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
}

@media only screen and (max-width: 600px) {
  .side-menu {
    font-size: 20px;
  }

  .hamburger.change {
    position: absolute;
    right: 10px;
    top: 10px;
  }

  .side-menu.opened{
    width: 100%;
    padding-top: 54px;
  }
}

