.main-header {
  margin-left: 55px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background-color: #ccc;
  height: 55px;
}

.main-header.change {
  margin-left: 220px;
}

.main-header__title {
  padding: 0 10px;
}

.main-header__title h1 {
  padding: 10px 0;
  margin: 0;
}

@media only screen and (max-width: 600px) {
  .main-header.change {
    margin-left: 55px;
  }
}

input {
  font-size: 16px;
  font-weight: normal;
  border: none;
  border-bottom: 1px solid #546e7a;
}

.task__time-input {
  -moz-appearance: textfield;
  -webkit-appearance: textfield;
  width: 100px;
  text-align: center;
}

input[type="time"]::-webkit-outer-spin-button,
input[type="time"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="time"]::-webkit-clear-button {
    display: none;
}

input:focus,
textarea:focus {
  outline: none;
  border-bottom: 2px solid #2ea790;
}

.taskInput {
  display: none;
}/* TODO: dele this */

textarea {
  overflow: hidden;
  padding: 0;
  outline: none;
  resize: none;
  height: 20px;
  width: 380px;
  border: none;
  border-bottom: 1px solid #546e7a;
  font-size: 16px;
  line-height: 18px;
  font-weight: normal;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI",  "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  margin: 0 10px;
}

.task {
  margin-bottom: 10px;
  padding: 5px;
  width: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  background-color: #fff;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.task__time-button,
.task__delete-button {
  margin: 0 5px;
  background-color: inherit;
  border: none;
  font-size: 20px;
  padding: 0;
  width: 25px;
  height: 25px;
  color: #000;
}

.task__time-button:hover,
.task__delete-button:hover {
  color: #ccc;
  background-color: inherit;
}

.task__delete-button {
  margin-left: 20px;
}

.task__period {
  width: 100px;
  cursor: pointer;
}

.task__time-button {

}

@media only screen and (max-width: 600px) {
  .task {
    width: 100%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    font-size: 20px;
  }

  .task__name {
    width: 100%;
    margin: 10px 0;
  }

  .task__delete-button {
    margin-left: auto;
    background-color: inherit;
    width: 30px;
    height: 30px;
    font-size: 30px;
  }

  .task__time-input {
    width: 65px;
    font-size: 20px;
    line-height: 20px;
    height: 30px;
  }

  .task__time-button {
    font-size: 30px;
    width: 30px;
    height: 30px;
  }
}

.task__name {
  box-sizing: border-box;
  border: none;
  border-radius: 3px;
  resize: none;
  font-size: 16px;
  line-height: 18px;
  overflow: hidden;
  height: auto;
  padding: 5px;
  border-bottom: 1px solid #546e7a;
  box-shadow: 0px 4px 4px -6px black;
}

.task-list {
  width: 100%;
  font-size: 16px;
  line-height: 25px;
}

.total-time {
  font-size: 18px;
}

.total-time__time {
  font-weight: 500;
  cursor: pointer;
}

.calculated-list {
  font-size: 18px;
}

.calculated-list__hedaer {
  font-weight: 500;
  margin-bottom: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}

.calculated-list__title-time,
.calculated-list__task-time {
  min-width: 80px;
}

.calculated-list__item {
  margin-bottom: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;

}

.calculated-list__task-time,
.calculated-list__task-name {
  cursor: pointer;
}

.time-tracker {
  padding: 10px;
  width: 620px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.time-tracker__header {
  margin-bottom: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.time-tracker__footer {
  margin-bottom: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.button-clear {
  background-color: #ed6a40;
}

.button-clear:hover {
  background-color: #f19376;
}


@media only screen and (max-width: 600px) {
  .time-tracker {
    width: 100%;
  }
}

