.main-header {
  margin-left: 55px;
  transition: 0.5s;
  background-color: #ccc;
  height: 55px;
}

.main-header.change {
  margin-left: 220px;
}

.main-header__title {
  padding: 0 10px;
}

.main-header__title h1 {
  padding: 10px 0;
  margin: 0;
}

@media only screen and (max-width: 600px) {
  .main-header.change {
    margin-left: 55px;
  }
}
